
#app {
  background-color: #e1e1e1;
  min-height: 100vh;
}
#app .container {
    width: 1024px;
    margin: 0 auto;
}

.navi {
  width: 1024px;
  height: 81px;
  margin: 0 auto;
  background-color: #4a90e2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.navi .box {
    width: 660px;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.navi .box .item {
      width: 220px;
      line-height: 76px;
      color: #00408b;
      font-size: 24px;
      text-align: center;
}
.navi .box .item:nth-child(1) {
        color: #fff;
        border-bottom: 5px solid #00408b;
}

.addTask {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
  margin-top: 30px;
  width: 620px;
  height: 65px;
  border-radius: 5px;
  background-color: #fff;
  border: 2px solid #c8c8c8;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #c8c8c8;
  padding-left: 30px;
  font-size: 24px;
}
input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 5px;
}
.addItem {
  width: 620px;
  margin: 0 auto;
  margin-top: 30px;
  -webkit-box-shadow: 0 4px 4px 0 #c8c8c8;
          box-shadow: 0 4px 4px 0 #c8c8c8;
}
.addItem .top {
    line-height: 76px;
    background-color: #fff2dc;
    font-size: 24px;
    border-radius: 5px 5px 0 0;
}
.addItem .top .box {
      margin-left: 96px;
}
.addItem .top .box input {
        width: 350px;
        height: 40px;
}
.addItem .top .box .right {
        float: right;
        margin-right: 50px;
}
.addItem .top .box .right span {
          margin-right: 15px;
}
.addItem .content {
    padding: 20px 0;
    background-color: #f2f2f2;
}
.addItem .content .item {
      width: 465px;
      margin: 0 auto;
      margin-top: 20px;
}
.addItem .content .item .text {
        font-size: 20px;
}
.addItem .content .item .data {
        margin-left: 20px;
}
.addItem .content .item .data textarea {
          width: 465px;
          resize: none;
}
.addItem .bottom {
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-radius: 0 0 5px 5px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.addItem .bottom .item {
      width: 100%;
      font-size: 24px;
      line-height: 60px;
      text-align: center;
      border-radius: 0 0 5px 5px;
}
.addItem .bottom .item:first-child {
        background-color: #fff;
        color: #d0021b;
}
.addItem .bottom .item:last-child {
        background-color: #4a90e2;
        color: #fff;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.container .todoItem {
    width: 620px;
    margin: 0 auto;
    margin-top: 30px;
    line-height: 76px;
    border-radius: 5px;
    background-color: #fff;
    font-size: 24px;
    -webkit-box-shadow: 0 4px 4px 0 #c8c8c8;
            box-shadow: 0 4px 4px 0 #c8c8c8;
}
.container .todoItem .box {
      margin-left: 96px;
}
.container .todoItem .box .line {
        text-decoration: line-through;
}
.container .todoItem .box .right {
        float: right;
        margin-right: 50px;
}
.container .todoItem .box .right span {
          margin-right: 15px;
}
.container .todoItem .box .right span .fas {
            color: #F5A623;
}
.container .todoItem.starItem {
    background-color: #fff2dc;
    -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
            order: -1;
}
.container .todoItem.statusItem {
    -webkit-box-ordinal-group: 1000;
        -ms-flex-order: 999;
            order: 999;
}
